.pdfFrame{
    position: fixed;
    z-index: 5;
    background-color: rgba(17, 17, 17, 0.336);
    width: 0;
    height: 100vh;
    top: 0;
    left: 0;
    overflow: hidden;
    transition: width .5s;
}

.toggled{
    width: 100%;
    height: 100vh;
}

.iframeContainer{
    width: 100%;
    height: 80vh;
}

iframe{
    width: 100%;
}